.topBotomBordersOut span:before, div.topBotomBordersOut span:after
{
    position: absolute;
    left: 0px;
    width: 100%;
    height: 1px;
    background: royalblue;
    content: "";
    opacity: 0;
    transition: all 0.3s;
}

.topBotomBordersOut span:before
{
    top: 0px;
    transform: translateY(10px);
}

.topBotomBordersOut span:after
{
    bottom: 0px;
    transform: translateY(-10px);
}

.topBotomBordersOut span:hover:before, div.topBotomBordersOut span:hover:after
{
    opacity: .5;
    transform: translateY(0px);
   
}


